export const scores = Object.assign(
  {},
  ...Object.values(scoreAreas).map(element => element.components),
);

export const scoreKeys = Object.keys(scores);

export function Trs() {
  this.a = {
    lab: '',
    diagnosis: '',
    date: '',
    dateofbirth: '',
    dateofaccident: '',
    phase: '',
  };
  scoreKeys.forEach((element) => {
    if (clinicalSelects[element].multiple) {
      this.a[element] = [];
    } else {
      this.a[element] = 0;
    }
  });
}

export function cloneTrs(sample) {
  const clone = new Trs();
  clone.a = { ...sample.a };
  return clone;
}

export function elementScore(key, value) {
  const mapping = scores[key];
  if (Array.isArray(value)) {
    const reducer = (accumulator, currentValue) => (
      accumulator + mapping[currentValue]
    );
    return value.reduce(reducer, 0);
  }
  return mapping[value];
}

export function getScoreArea(key) {
  let needle = '';
  Object.entries(scoreAreas).forEach(([areaKey, area]) => {
    if (key in area.components) {
      needle = areaKey;
    }
  });
  return needle;
}

export function calculateScore(trs) {
  const reducer = (accumulator, currentValue) => (
    accumulator + elementScore(currentValue, trs.a[currentValue])
  );
  return 100 - scoreKeys.reduce(reducer, 0);
}
